import { pathOr, map, keys, flatten, forEach } from 'ramda'
import useSWR from '@/hooks/useSWR'
import { QueryVariables, Mutation, MutationOptions } from '@/constants/types'
import { Doctor } from '@/services/pathology'
import { useRequest } from '@/utils/request'
import { mapIndexed } from '@/utils/webHelper'
const { compose } = require('ramda')

export type QueryResult = {
  yearMonth: string
  date: string
  doctor: string
  period: string
}

export function useShifts({
  lazy,
  onCompleted,
  onError,
  ...variables
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<QueryResult[]>(
    `/api/v1/d1/getDoctorShift/{date}`,
    {
      variables,
      onCompleted,
      onError,
      lazy,
    }
  )

  let shiftById: { [key: string]: QueryResult[] } = {}

  compose(
    forEach((x: QueryResult) => {
      if (!shiftById[x.date]) {
        shiftById[x.date] = [x]
      } else {
        shiftById[x.date].push(x)
      }
    }),
    pathOr([], [])
  )(data)

  return {
    ...rest,
    shiftById,
    data,
  }
}

export function useCreateOrUpdateShift(options: MutationOptions = {}) {
  const { loading, mutate } = useRequest(`/api/v1/d1/createShift`, {
    ...options,
    method: 'POST',
  })

  const handler = (values: {
    date: moment.Moment
    doctorList: { [key: number]: any[] }
  }) => {
    let payload = compose(
      flatten,
      map((key: React.Key) =>
        compose(
          mapIndexed((x: Doctor, idx: number) => ({
            date: +key,
            year_month: values.date.format('YYYY-MM'),
            period: idx === 0 ? '00:00~1200' : '12:00~24:00',
            doctor: x.doctor,
          })),
          pathOr([], ['doctorList', key])
        )(values)
      ),
      keys,
      pathOr({}, ['doctorList'])
    )(values)

    return mutate(payload)
  }

  return [loading, handler] as Mutation
}
