import React from 'react'
import { DragSource, ConnectDragPreview, ConnectDragSource } from 'react-dnd'
import { getEmptyImage } from 'react-dnd-html5-backend'
import { Doctor } from '@/services/pathology'
import ItemTypes from './item-types'

type Props = {
  item: Doctor
  index?: number
  style?: React.CSSProperties
  className?: string
  draggable?: boolean
  isDragging?: boolean
  connectDragPreview?: ConnectDragPreview
  connectDragSource?: ConnectDragSource
}

const withDragSource = DragSource(
  ItemTypes.CARD,
  {
    beginDrag(props: Props) {
      return {
        ...props.item,
      }
    },
    canDrag() {
      return true
    },
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    connectDragPreview: connect.dragPreview(),
    isDragging: monitor.isDragging(),
  })
)

class DoctorCard extends React.Component<Props> {
  componentDidMount = () => {
    const { connectDragPreview } = this.props
    if (connectDragPreview) {
      connectDragPreview(getEmptyImage() /* { anchorX: 0.2, anchorY: 0.1 } */)
    }
  }

  render() {
    const {
      item,
      className,
      style,
      connectDragSource = (node: any) => node,
      connectDragPreview = (node: any) => node,
      isDragging,
    } = this.props

    const _style = {
      ...style,
      border: `1px ${isDragging ? 'dashed' : 'solid'} #ddd`,
    }

    return connectDragSource(
      connectDragPreview(
        <div key={item.id} className={className} style={_style}>
          醫師 {item.doctor}
        </div>
      )
    )
  }
}

export default withDragSource(DoctorCard)
