import React from 'react'
import { SelectProps } from 'antd/lib/select'
import useSwr from 'swr'
import { pathOr, head, forEach } from 'ramda'
import request from '@/utils/request'
import useSWR from '@/hooks/useSWR'
import Select from '@/components/select'
import { QueryVariables, MedicalOrder } from '@/constants/types'
import { uuid } from '@/utils/webHelper'
import { Divisions } from '@/constants/enums'
const { compose } = require('ramda')

export type Doctor = {
  id: number
  doctorId: number
  doctor: string
  division: Divisions
}

export function usePathology() {
  const { data, ...others } = useSwr(`/api/b2/getPathologyResult`, url =>
    request(url, {
      method: 'POST',
      body: {
        classification: 'pathorderview',
        noType: 'HHISNUM',
        no: '16685033',
      },
    })
  )

  return {
    ...others,
    data: compose(head, pathOr([], []))(data) as MedicalOrder,
  }
}

export function useDoctors({
  division = Divisions.PATH,
  ...variables
}: QueryVariables = {}) {
  const { data = [], ...rest } = useSWR<Doctor[]>(
    '/api/v1/c2/getC2DoctorList/{division}',
    {
      variables: { division, ...variables },
    }
  )

  const _data = data?.map(x => ({ ...x, id: x.doctorId }))

  const doctorById: { [key: string]: any } = {}

  forEach(s => (doctorById[s.doctorId] = s), _data)

  return {
    ...rest,
    doctorById,
    data: _data,
    doctorEnums: _data,
  }
}

export function DoctorSelect(props: SelectProps<any>) {
  const { data } = useDoctors()
  return (
    <Select {...props}>
      {data.map(x => (
        <Select.Option key={x.id} value={x.doctor}>
          {x.doctor}
        </Select.Option>
      ))}
    </Select>
  )
}

export function useBodyParts() {
  const { data = [], ...rest } = useSWR<string[]>('/api/v1/b1/getBodyParts')

  const _data = data?.map(x => ({ id: uuid(), name: x }))

  return {
    ...rest,
    data: _data,
  }
}

