import React from 'react'
import moment from 'moment'
import { Form, Row, Col, DatePicker, Spin, message } from 'antd'
import { invoker, identity, times, pathOr, forEach, map } from 'ramda'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import DragLayer from '@/components/snippet-drag-layer'
import DndProvider from '@/components/snippet-scrollable-container'
import {
  useShifts,
  useCreateOrUpdateShift,
  QueryResult,
} from '@/services/shift'
import { useDoctors, Doctor } from '@/services/pathology'
import DraggableDoctorCard from './draggable-doctor-card'
import ImageSchedulerCalendar from './image-scheduler-calendar'
const { compose } = require('ramda')

const MonthPicker = DatePicker.MonthPicker

export default function ImageScheduler() {
  useTitle('影像排班')

  useBreadcrumb([{ name: '影像排班' }])

  const handleConfirm = useRouteLeaveConfirm('/')

  const [date, setDate] = React.useState(moment())

  const [doctorList, setDoctorList] = React.useState<{ [key: string]: any[] }>(
    {}
  )

  const { refetch, loading, called, revalidate } = useShifts({
    date: date.format('YYYY-MM'),
    lazy: true,
    onCompleted: (data: QueryResult[]) => {
      const shiftById: { [key: string]: QueryResult[] } = {}

      compose(
        forEach((x: QueryResult) => {
          const data = { ...x, id: `${x.date}${x.period}`, index: x.date }

          if (!shiftById[x.date]) {
            shiftById[x.date] = [data]
          } else {
            shiftById[x.date].push(data)
          }
        }),
        pathOr([], [])
      )(data)

      setDoctorList(shiftById)
    },
  })

  const [validating, handleSave] = useCreateOrUpdateShift({
    onCompleted: response => {
      revalidate()
      message.success('儲存成功')
    },
  })

  const handleOk = () => {
    handleSave({ date, doctorList })
  }

  useHeaderContent(
    <div className="inline-flex">
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleOk}>
        儲存
      </Button>
      <Button
        color="outline"
        className="ml-6 bg-transparent text-white border-white"
        onClick={handleConfirm}>
        放棄
      </Button>
    </div>
  )

  const { doctorEnums } = useDoctors()

  const handleMonthChange = (value: moment.Moment | null) => {
    if (value) {
      setDate(value)
    }
  }

  const handleSearch = () => {
    refetch({ date: date.format('YYYY-MM') })
  }

  const handleDoctorDrop = ({ index, ...data }: any) => {
    setDoctorList(prev => {
      const list = pathOr([], [index])(prev)
      return {
        ...prev,
        [index]: [...list, { index, ...data }],
      }
    })
  }

  const handleDoctorRemove = ({ index, ...data }: any) => {
    setDoctorList(prev => {
      const list = pathOr([], [index])(prev)
      return {
        ...prev,
        [index]: list.filter((x: any) => x.id !== data.id),
      }
    })
  }

  let calendar: JSX.Element[] = []

  compose(
    forEach((idx: number) => {
      calendar.push(
        <Col key={`calendar-${idx}`} span={3}>
          <div className="py-4 bg-gray-300 text-center">
            {date.month() + 1}/{idx + 1}
          </div>
          <div className="h-20 border border-gray-300">
            <ImageSchedulerCalendar
              index={idx}
              doctors={pathOr([], [idx])(doctorList)}
              onDoctorDrop={handleDoctorDrop}
              onDoctorRemove={handleDoctorRemove}
            />
          </div>
        </Col>
      )
    }),
    times(identity),
    invoker(0, 'getDate')(),
    invoker(0, 'toDate')()
  )(date)

  return (
    <DndProvider>
      <DragLayer />
      <Spin spinning={loading || validating}>
        <Page>
          <Row className="mb-6">
            <Col span={24}>
              <div className="rounded-xl p-4 bg-white">
                <Form layout="inline" colon={false}>
                  <Form.Item label="排班年月">
                    <MonthPicker
                      allowClear={false}
                      value={date}
                      onChange={handleMonthChange}
                    />
                  </Form.Item>
                  <Button
                    color="primary"
                    className="ml-4"
                    onClick={handleSearch}>
                    查詢
                  </Button>
                </Form>
              </div>
            </Col>
          </Row>
          {called && (
            <>
              <Row className="mb-6">
                <Col span={24}>
                  <h2 className="text-base font-normal">醫師列表</h2>
                  <div className="bg-white rounded-xl p-4">
                    <Row gutter={8}>
                      {map(
                        (x: Doctor) => (
                          <Col span={3} key={x.id}>
                            <DraggableDoctorCard
                              draggable
                              item={x}
                              className="bg-login-cyan text-white flex items-center justify-center px-6 py-4 mb-2 cursor-move"
                            />
                          </Col>
                        ),
                        doctorEnums
                      )}
                    </Row>
                  </div>
                </Col>
              </Row>
              <Row>
                <Col span={24}>
                  <h2 className="text-base font-normal">排班列表</h2>
                  <div className="bg-white rounded-xl p-4">
                    <Row>{calendar}</Row>
                  </div>
                </Col>
              </Row>
            </>
          )}
        </Page>
      </Spin>
    </DndProvider>
  )
}
