import React from 'react'
import cx from 'classnames'
import { CloseOutlined } from '@ant-design/icons'
import { DropTarget, ConnectDropTarget } from 'react-dnd'
import styled from 'styled-components'
import { lt, __, length, allPass, propEq, find, isNil } from 'ramda'
import { Doctor } from '@/services/pathology'
import ItemTypes from './item-types'
const { compose } = require('ramda')

interface Props {
  draggingItem?: any
  isOver?: boolean
  index: number
  canDrop?: boolean
  doctors: any[]
  className?: string
  style?: React.CSSProperties
  connectDropTarget: ConnectDropTarget
  onDoctorDrop(data: any): void
  onDoctorRemove(data: any): void
}

const BlockPlaceHolder = styled.div.attrs({
  className: 'px-6 py-4 block-placeholder',
})`
  display: flex;
  align-items: center;
  border: rgba(225, 225, 225, 0.9) 2px dashed;
  background-color: transparent;
`

const withDropTarget = DropTarget<Props>(
  ItemTypes.CARD,
  {
    drop({ index }, monitor, component) {
      const item = monitor.getItem()

      component.handleDoctorDrop(item, index)
    },
    canDrop({ doctors }, monitor) {
      const item = monitor.getItem()

      return allPass([
        compose(lt(__, 2), length),
        compose(isNil, find(propEq('id', item.id))),
      ])(doctors)
    },
  },
  (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    canDrop: monitor.canDrop(),
    didDrop: monitor.didDrop(),
    draggingItem: monitor.getItem(),
  })
)

class ImageSchedulerCalendar extends React.Component<Props> {
  handleDoctorDrop = (item: any, index: number) => {
    this.props.onDoctorDrop({ ...item, index })
  }

  handleDoctorRemove = (x: any) => () => {
    this.props.onDoctorRemove(x)
  }

  render() {
    const {
      connectDropTarget,
      isOver,
      canDrop,
      doctors = [],
      className,
      style,
    } = this.props

    let itemHeight = `${100 / (doctors.length > 0 ? 2 : 1)}%`

    return connectDropTarget(
      <div
        className={cx('w-full', className)}
        style={{ height: '100%', ...style }}>
        {doctors.map((x: Doctor) => (
          <div
            key={`scheduled-doctor-${x.id}`}
            className="bg-login-cyan text-white flex items-center justify-center px-6 py-4 relative"
            css={{
              height: itemHeight,
              marginBottom: '0.1rem',
            }}>
            <div>
              {x.doctor}
              <CloseOutlined
                onClick={this.handleDoctorRemove(x)}
                style={{
                  position: 'absolute',
                  right: 5,
                  top: 9,
                  fontSize: 12,
                  cursor: 'pointer',
                }}
              />
            </div>
          </div>
        ))}
        {isOver && canDrop && (
          <BlockPlaceHolder style={{ height: itemHeight }} />
        )}
      </div>
    )
  }
}

export default compose(withDropTarget)(ImageSchedulerCalendar)
